ul li a {
  display: block;
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  padding: 10px;
  transition: 0.2s;
}

ul li a:hover {
  transform: translateY(10px);
}

ul li a.active {
  border-top: 10px crimson solid;
  transform: translateY(0);
}
